<template>
  <div id="CompanyIntroduction">
    <div class="banner container-fuild text-center">关于我们</div>
    <div class="container">
      <div class="row CompanyIntroduction-container">
        <div class="col-xs-12 col-sm-12 col-md-6 wow zoomIn">
          <img class="img-responsive center-block" src="@/assets/img/about_img.png" />
        </div>
        <div class="col-xs-12 col-sm-12 col-md-6">
          <h3>深圳市睿捷铭科技有限公司</h3>
          <p
            class=".text-justify"
          >深圳睿捷铭科技有限公司是一家专业为食品安全、环境监测、高校科研等单位提供实验室仪器、分析测试解决方案和实验室整体改造的企业。公司集生产、研发、代理、销售及技术服务为一体全方位服务各个领域的客户。</p>
          <p
            class=".text-justify"
          >公司自成立以来，凭借专业的技术实力背景将全自动样品前处理概念引入中国，特别是全自动固相萃取领域保持国内优越地位。后逐渐引进瑞士万通电位滴定以及水分仪、屹尧科技微波消解仪、美国J2 scientific公司全自动样品前处理平台、美国Agilent公司（原英国PL）凝胶色谱仪系列产品、美国Xylem公司（原美国OI）吹扫捕集及TOC系列产品、普立泰科公司多功能氮吹浓缩仪、语瓶公司洗瓶机等产品。</p>
          <p
            class=".text-justify"
          >公司秉承“Best for you”的核心理念。公司只代理质量和性能值得信赖的产品，致力于服务技术领先者，以此赢得了业界和用户的一致好评和信赖。</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from "wowjs";
export default {
  name: "CompanyIntroduction",
  data() {
    return {};
  },
  mounted() {
    var wow = new WOW();
    wow.init();
  }
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner1.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
.row {
  margin-right: 0;
  margin-left: 0;
}
.CompanyIntroduction-container {
  padding: 100px 0;
  color: #808080;
  transition: all ease 0.5s;
}
.CompanyIntroduction-container > div > p {
  font-size: 14px;
  line-height: 2.5rem;
}
@media screen and (max-width: 997px) {
  .CompanyIntroduction-container {
    padding: 10px 0;
    color: #808080;
  }
}
</style>




